// Base
.banner {
    width: 100%;
    position: relative;
    padding: @spacing-wide 0;

    >.banner-title{
        font-weight: bold;
        margin-top: 0;
        font-size: @font-size-h5;
    }
    >.banner-subtitle {
        margin-top: 0;
        font-size: @font-size-h6;
    }
    @media (min-width: @screen-medium-min){
        padding: 2rem 0;
        >.banner-title{
            font-size: @font-size-h3;
        }
        >.banner-subtitle {
            font-size: @font-size-h4;
        }
    }

    &.inversed {
        color: @color-snow;
        >.banner-title{
            color: @color-snow;
        }
        >.banner-subtitle {
            color: @color-snow;
        }
    }
}

