// Base
.group{
    .clearfix();
    &.group--button {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin-right: @spacing-small;
        &:last-child {
            margin-right: 0;
        }
        > .button {
            position: relative;
            border-radius: 0;
            float: left;
            margin-left: -1px;
            margin-right: 0;
        }
        .group-button(button;@border-radius-base);
        .group-button(button--tiny;@border-radius-tiny);
        .group-button(button--small;@border-radius-small);
        .group-button(button--medium;@border-radius-medium);
        .group-button(button--large;@border-radius-large);
        .group-button(button--huge;@border-radius-huge);
        .group-button(button--wide;@border-radius-wide);
    }

    &.group--dropdown{
        line-height: 1;
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin:0;
        margin-right: @spacing-small;
        &:last-child {
            margin-right: 0;
        }
        > .dropdown {
            > .dropdown-trigger {
                > .button {
                    margin-left: -1px;
                    border-radius: 0;
                }
            }
        }
        .group-dropdown(button;@border-radius-base);
        .group-dropdown(button--tiny;@border-radius-tiny);
        .group-dropdown(button--small;@border-radius-small);
        .group-dropdown(button--medium;@border-radius-medium);
        .group-dropdown(button--large;@border-radius-large);
        .group-dropdown(button--huge;@border-radius-huge);
        .group-dropdown(button--wide;@border-radius-wide);
    }
}