// Base
.note {
	position: relative;
    border: 1px solid @color-gray-lighter;
    border-radius: 0;
    margin-bottom: @spacing-medium;
    padding: @spacing-medium 0;
    .clearfix();

    > .note-side {
        position: absolute;
        width: @side-width-medium;
        padding-left: @spacing-medium;
        left: 0;
        top: @spacing-medium;
    }

    > .note-main {
        margin-left: @side-width-medium;
        padding: 0 @spacing-medium ;
        overflow: hidden;

        > .note-main-head {
            > .note-main-head-title {
                width: 100%;
                margin-top: 0;
                margin-bottom: 0;
                font-size: @font-size-h4;
            }
        }
        > .note-main-nose {
            color: @color-gray-light;
            font-size: 80%;
        }
        > .note-main-neck {
            margin: 1rem 0;
        }
        > .note-main-body {
            > p {
                margin-bottom: 1rem;
            }
        }
    }

    &.note--small {
        > .note-side {
            width: @side-width-small;
        }
        > .note-main {
            margin-left: @side-width-small;
            > .note-main-head {
                > .note-main-head-title {
                    font-size: @font-size-h5;
                }
            }
            > .note-main-neck {
                font-size: 0.875rem;
                margin: 0.315rem 0;
            }
            > .note-main-body {
                > p {
                    font-size: 0.875rem;
                    margin-bottom: 0.315rem;
                }
            }
        }
    }

}

.note.note--simple {
    > .note-main {
        > .note-main-head {
            > .note-main-head-title {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space:nowrap;
                line-height: 1.5;
            }
        }
        > .note-main-neck {
            height: 3rem;
            overflow: hidden;
            margin: 0;
            > * {
                margin: 0;
                height: 3rem;
                overflow: hidden;
            }
        }
        > .note-main-body {
            display: none;
        }
    }
    &.note--small {
        > .note-main {
            > .note-main-neck {
                display: none;
            }
        }
    }
}