.group-button(@class-button; @border-radius){
  > .@{class-button} {
    &:hover,&:focus,&:active,&.selected {
      z-index: 2;
    }
    &:first-child {
      .border-left-radius(@border-radius);
      margin-left: 0;
    }
    &:last-child {
      .border-right-radius(@border-radius);
    }
  }
}

.group-dropdown(@class-button; @border-radius) {
  > .dropdown {
    &:first-child {
      > .dropdown-trigger {
          > .@{class-button} {
            .border-left-radius(@border-radius);
            margin-left: 0;
          }
      }
    }
    &:last-child {
      > .dropdown-trigger {
        > .@{class-button} {
          .border-right-radius(@border-radius);
        }
      }
    }
  }
}