// Base
.overlay
{
    z-index: @z-index-portal;

    width: 100%;
    height: 100%;

    pointer-events: none;

    &.invisible > *:not(.overlay-mask)
    {
        pointer-events: all;
    }
    > .overlay-mask
    {
        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        opacity: 0;
        background-color: @color-snow;
    }
    > .overlay-warpper
    {
        position: relative;
        z-index: @z-index-portal + 1;

        max-width: @warpper-wide;
        margin: 0 auto;
        padding: 0 (@spacing-base / 2);

    }
    &.activated
    {
        pointer-events: all;
        > .overlay-mask
        {
            opacity: .55;
        }
    }
}

.overlay--modal
{
    > .overlay-mask
    {
        background-color: @color-snow;
    }
    > .overlay-warpper
    {
        height: 98vh;
        padding-top: 1vh;
        @media (min-width: @screen-large-min)
        {
            height: 96vh;
            padding-top: 2vh;
        }
        > .modal
        {
            height: 100%;
        }
    }
    &.activated
    {
        > .overlay-mask
        {
            opacity: 1;
        }
    }
}
