// Base
*
{
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-touch-callout: none;
}
*:before,
*:after
{
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
}
*::selection
{
    background-color: @background-selection;
}

/* 设定根字体的大小 */
html
{
    font-family: @font-family;
    font-size: 100%;  // 16px = 12pt
    font-weight: 300;
    line-height: @line-height-base;

    height: 100%;

    color: @color-gray-dark;
    background-color: @background-html;

    -webkit-text-size-adjust: 100%;
        -ms-text-size-adjust: 100%;
    text-rendering: optimizelegibility;
    @media (min-width: @screen-large-min)
    {
        font-size: 112.5%; // 18px = 14pt
    }
}

body
{
    min-height: 100%;

    background-color: @background-body;
    overflow-x: hidden;
    overflow-y: scroll;
}

body::-webkit-scrollbar
{
    width: 0px;
}

body::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
}

body::-webkit-scrollbar-thumb
{
    outline: 1px solid @color-gray-light;
    background-color: @color-gray-light;
}

/* 清除所有元素的内外边框 */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section
{
    margin: 0;
    padding: 0;
}

/* 重设HTML标签 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary
{
    display: block;
}

/* 使HTML5的媒体标签跟 img 保持一致 */
audio,
canvas,
progress,
video
{
    display: inline-block;

    vertical-align: baseline;
}

/* 标准化图片的显示 */
img
{
    border: 0;
}

svg:not(:root)
{
    overflow: hidden;
}

/* 标准化列表项默认样式 */
ul,
ol
{
    list-style: none;
}

/* 强制表单元素不继承父级的字体属性 */
button,
input,
select,
optgroup,
textarea
{
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;

    margin: 0;

    color: inherit;
    outline: 0;
}

/* 一致化表单按钮与输入框的显示 */
button
{
    overflow: visible;
}

button,
select
{
    text-transform: none;
}

button,
html input[type='button'],
input[type='reset'],
input[type='submit']
{
    -webkit-appearance: button;
}

button[disabled],
html input[disabled]
{
    cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner
{
    padding: 0;

    border: 0;
}

input
{
    line-height: normal;
}

input[type='checkbox'],
input[type='radio']
{
    box-sizing: border-box;
    padding: 0;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button
{
    height: auto;
}

input[type='search']
{
    -webkit-appearance: textfield;
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration
{
    -webkit-appearance: none;
}

input::-ms-clear,
input::-ms-reveal
{
    display: none;
}

optgroup
{
    font-weight: normal;
}

/* 重设表单其它元素的显示 */
legend
{
    padding: 0;

    border: 0;
}

fieldset
{
    padding: 0;

    border: 0;
}

textarea
{
    overflow: auto;
}

/* 重设隐藏的相关属性 */
audio:not([controls])
{
    display: none;

    height: 0;
}

[hidden],
template
{
    display: none;
}

/* 可拖动文件添加拖动手势 */
[draggable]
{
    cursor: move;
}

/* 重设表格的默认样式 */
table
{
    border-spacing: 0;
    border-collapse: collapse;
}

caption,
th
{
    text-align: left;
}

td,
th
{
    padding: 0;
}

/* 重设超链接的默认样式 */
a
{
    cursor: pointer;
    text-decoration: none;

    color: @link-color;
    outline: none;
    background: transparent;
    &:hover
    {
        color: @link-hover-color;
    }
    &:active
    {
        color: @link-active-color;
    }
    &:active,
    &:hover
    {
        text-decoration: none;

        outline: 0;
    }
    &[disabled]
    {
        cursor: not-allowed;
        pointer-events: none;

        color: #ccc;
    }
}
