// Base
.header
{
    position: relative;

    margin-right: (@spacing-base / -2);
    margin-left: (@spacing-base / -2);

    @media (min-width: @screen-medium-min)
    {
        margin-right: 0;
        margin-left: 0;
    }

    > .header-control
    {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        text-align: center;
        width:@layout-header-height;
        height: @layout-header-height;
        line-height: @layout-header-height;
        border-left: 1px solid transparent;
        border-right: 1px solid transparent;
        overflow: hidden;
        z-index: @z-index-layout + 100;
    }
    > .header-navbar
    {
        min-height: @layout-header-height;
        padding: 0 ;

        background: transparent;
    }
    > .header-chatbar
    {
        min-height: @layout-header-height;
        width: 100%;
        position: absolute;
        padding: 0;
        left: 0;
        top: 0;

        background: transparent;
    }

    &.advanced
    {
        > .header-control
        {

            display: block;
            border-right-color: @color-gray-lighter;

            @media (min-width: @screen-medium-min)
            {
                border-right-color: transparent;
            }
        }
        > .header-navbar
        {
            padding-left: @layout-header-height;
        }
        > .header-chatbar
        {
            padding-left: @layout-header-height;
        }
    }
}

// Addon
.header {
    &.advanced {
        > .header-navbar {
            > .navbar {
                &.advanced {
                    > .navbar-brand {
                        display: none;
                        @media (min-width: @screen-medium-min) {
                            display: block;
                        }
                    }
                    > .navbar-content {
                        padding-left: 0;
                        @media (min-width: @screen-medium-min) {
                            padding-left: (@navbar-height-large * 2);
                        }
                    }
                    > .navbar-surplus {
                        border-left-color: @color-gray-lighter;
                        @media (min-width: @screen-medium-min) {
                            border-left-color: transparent;
                        }
                    }
                }
            }
        }
    }
}