// Base
.icon {
  display:inline-table;
  vertical-align: middle;
  margin-top: -0.175em;

  &.icon--brand {
    color: @color-brand;
  }
  &.icon--default {
    color: @color-default;
  }
  &.icon--primary {
    color: @color-primary;
  }
  &.icon--success {
    color: @color-success;
  }
  &.icon--warning {
    color: @color-warning;
  }
  &.icon--error {
    color: @color-error;
  }
}