// Base
.timeline {

}

.timeline-item {
    position: relative;
    padding-bottom: @spacing-base;

    > .timeline-item-tail {
        position: absolute;
        left: 0.445rem;
        top: 0;
        height: 100%;
        border-left: 2px solid @color-gray-lighter;
    }

    > .timeline-item-origin {
        position: absolute;
        width: 1rem;
        height: 1rem;
        background-color: @color-white;
        border-radius: 100%;
        border: 2px solid @color-gray-lighter;
    }

    > .timeline-item-content {
        padding: 0 0 @spacing-tiny @spacing-huge;
        font-size: @font-size-small;
        position: relative;
        top: -3px;
        > a {
            color: @color-gray-base;
        }
    }

    &:last-child {
        > .timeline-item-tail {
            display: none;
        }
    }

    // skin
    &.timeline-item--brand {
        > .timeline-item-origin {
            border-color: @color-brand;
        }
    }
    &.timeline-item--default {
        > .timeline-item-origin {
            border-color: @color-default;
        }
    }
    &.timeline-item--primary {
        > .timeline-item-origin {
            border-color: @color-primary;
        }
    }
    &.timeline-item--success {
        > .timeline-item-origin {
            border-color: @color-success;
        }
    }
    &.timeline-item--warning {
        > .timeline-item-origin {
            border-color: @color-warning;
        }
    }
    &.timeline-item--error {
        > .timeline-item-origin {
            border-color: @color-error;
        }
    }
}