// Base
.modal {
    position: relative;
    margin: 0 auto;
    background-color: @color-white;
    overflow: auto;
    border: 1px solid @color-gray-lighter;
    border-radius: 0;
    z-index: @z-index-portal;

	&::-webkit-scrollbar{
		width: 0px;
	}

	&::-webkit-scrollbar-track{
		-webkit-box-shadow: inset 0 0 1px rgba(0,0,0,.2);
	}

	&::-webkit-scrollbar-thumb{
		outline: 0px solid @color-snow;
		background-color: @color-gray-lighter;
	}
    &.activated {
        opacity: 1;
    }

    > .modal-head {
    	padding: @padding-base;
    	border-bottom: 1px dashed @color-gray-lighter;
    	> .button {
    		position: absolute;
    		left: @spacing-medium;
    		top: @spacing-medium;
    	}
    	> .modal-head-title {
    		margin: 0;
    		padding-left: 2.865rem;
    	}
    }

    > .modal-body {
    	padding: @padding-base;
    	height: auto;
    	overflow-y: auto;
    }
}