// Base
.navbar
{
    position: relative;

    width: 100%;

    > .navbar-brand,
    > .navbar-surplus
    {
        position: absolute;
        top:0;
        height: @navbar-height-base;
        line-height: @navbar-height-base;
        text-align: center;
    }

    > .navbar-brand{
        display: none;
        font-weight: bold;
        left: 0;
        width: (@navbar-height-base * 2) ;
    }

    > .navbar-surplus {
        display: block;
        right: 0;
        width:@navbar-height-base;
        border-left: 1px solid transparent;
        border-right: 1px solid transparent;
        >.dropdown{
            >.dropdown-trigger {
                .button {
                    color: @color-gray-lighter;
                }
                &.activated {
                    .button {
                        color: @color-default;
                    }
                }
            }
        }
    }

    > .navbar-content {
        min-height: @navbar-height-base;
        line-height: @navbar-height-base;
        padding-left: 0;
        padding-right: @navbar-height-base;
    }

    &.advanced {
        > .navbar-brand{
            display: block;
        }
        > .navbar-content {
            padding-left: (@navbar-height-base * 2);
        }
    }
}

// size
.navbar
{
    &.navbar--small{
        > .navbar-brand,
        > .navbar-surplus
        {
            height: @navbar-height-small;
            line-height: @navbar-height-small;
        }

        > .navbar-brand{
            width: (@navbar-height-small * 2) ;
        }

        > .navbar-surplus {
            width:@navbar-height-small;
        }

        > .navbar-content {
            min-height: @navbar-height-small;
            line-height: @navbar-height-small;
            padding-right: @navbar-height-small;
        }

        &.advanced {
            > .navbar-brand{
                display: block;
            }
            > .navbar-content {
                padding-left: (@navbar-height-small * 2);
            }
        }
    }

    &.navbar--large{
        > .navbar-brand,
        > .navbar-surplus
        {
            height: @navbar-height-large;
            line-height: @navbar-height-large;
        }

        > .navbar-brand{
            width: (@navbar-height-large * 2) ;
        }

        > .navbar-surplus {
            width:@navbar-height-large;
        }

        > .navbar-content {
            min-height: @navbar-height-large;
            line-height: @navbar-height-large;
            padding-right: @navbar-height-large;
        }

        &.advanced {
            > .navbar-brand{
                display: block;
            }
            > .navbar-content {
                padding-left: (@navbar-height-large * 2);
            }
        }
    }
}
