// Base
.field {
    position: relative;
    padding-top: @spacing-base;
    padding-bottom: @spacing-medium;
    overflow: hidden;

    > .field-label {
        position: absolute;
        pointer-events: none;
        left: 0;
        color: @color-gray-lighter;
        transition: 0.3s ease all;
        will-change: left, top, contents;
        padding: @spacing-tiny 0;
        font-size: @font-size-base;
        line-height: @line-height-base;
        &.field-label--fixed {
            top: 0;
            font-size: @font-size-small;
            line-height: 1;
        }
    }

    > .field-hint {
        position: absolute;
        transition: 0.3s ease all;
        color: @color-gray-lighter;
        padding: @spacing-tiny 0;
        font-size: @font-size-base;
        line-height: @line-height-base;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    > .field-help {
        padding: @spacing-tiny 0;
        font-size: 0.75rem;
        line-height: 1;
        color: @color-gray-light;
    }

    > .field-control {
        padding: 1px 0;
        > input,
        > textarea {
            width: 100%;
            border: 0;
            padding: @spacing-tiny 0;
            background-image: linear-gradient(@color-primary, @color-primary), linear-gradient(@color-gray-light, @color-gray-light);
            background-size: 0 2px, 100% 1px;
            background-repeat: no-repeat;
            background-position: center bottom, center calc(~"100% - 1px");
            background-color: rgba(0, 0, 0, 0);
            transition: background 0s ease-out;
            float: none;
            font-size: @font-size-base;
            line-height: @line-height-base;

          &[readonly],
          &[disabled],
          fieldset[disabled] & {
            background-color: rgba(0, 0, 0, 0);
          }

          &[disabled],
          fieldset[disabled] & {
            background-image: none;
            border-bottom: 2px dotted @color-gray-light;
            cursor: not-allowed;
          }
        }
    }

    &.focused {
        > .field-label {
            color: lighten(@color-gray-light,5%);
            top: 0;
            font-size: @font-size-small;
            line-height: @font-size-small;
        }

        > .field-control {
            > input,
            > textarea {
                outline: none;
                background-image: linear-gradient(@color-primary, @color-primary), linear-gradient(@color-gray-light, @color-gray-light);
                background-size: 100% 2px, 100% 1px;
                box-shadow: none;
                transition-duration: 0.3s;
            }
        }
    }

    &.visited {
        > .field-label {
            color: lighten(@color-gray-light,5%);
            top: 0;
            font-size: @font-size-small;
            line-height: @font-size-small;
        }
    }

}

// Skin
.field {
    &.field--success {
        .field-generate(@color-success);
    }

    &.field--warning {
        .field-generate(@color-warning);
    }

    &.field--error {
        .field-generate(@color-error);
    }
}