// skin
.button-skin(@text-color; @background-color) {
  color: @text-color;
  background-color: @background-color;
  border-color: darken(@background-color, 5%);
  &:hover {
    color: tint(@text-color, 15%);
    background-color: tint(@background-color, 15%);
    border-color: darken(@background-color, 5%);
  }
  &:active,
  &.selected {
    color: shade(@text-color, 5%);
    background-color: shade(@background-color, 5%);
    border-color: shade(@background-color, 5%);
  }
}

// kind hollow
.button-kind-hollow(@border-color){
  background: @color-white;
  border-color: @border-color;
  color: @border-color;
  &:hover {
    background: lighten(@border-color, 36%);
  }
}

// kind link
.button-kind-link(@text-color) {
  background: none;
  color: @text-color;
  border-color: transparent;
  &:hover,
  &:focus{
    color: tint(@text-color, 15%);
  }
  &:active,
  &.selected {
    color: shade(@text-color, 5%);
    background: none;
  }
}

