// calculate-grid-item
.calculate-grid-item(@index, @class, @type) when (@type = width) and (@index > 0) {
  .grid-item--@{class}--@{index} {
    width: percentage((@index / @grid-items));
  }
}

.calculate-grid-item(@index, @class, @type) when (@type = push) and (@index > 0) {
  .grid-item--@{class}--push--@{index} {
    left: percentage((@index / @grid-items));
  }
}

.calculate-grid-item(@index, @class, @type) when (@type = push) and (@index = 0) {
  .grid-item--@{class}--push--0 {
    left: auto;
  }
}

.calculate-grid-item(@index, @class, @type) when (@type = pull) and (@index > 0) {
  .grid-item--@{class}--pull--@{index} {
    right: percentage((@index / @grid-items));
  }
}

.calculate-grid-item(@index, @class, @type) when (@type = push) and (@index = 0) {
  .grid-item--@{class}--pull--0 {
    right: auto;
  }
}

.calculate-grid-item(@index, @class, @type) when (@type = offset) {
  .grid-item--@{class}--offset--@{index} {
    margin-left: percentage((@index / @grid-items));
  }
}

.loop-grid-items(@index, @class, @type) when (@index >= 0) {
  .calculate-grid-item(@index, @class, @type);
  .loop-grid-items((@index - 1), @class, @type);
}

.make-grid-items(@class) {
  .loop-grid-items(@grid-items, @class, width);
  .loop-grid-items(@grid-items, @class, pull);
  .loop-grid-items(@grid-items, @class, push);
  .loop-grid-items(@grid-items, @class, offset);
}

