// Base
.tab {
    display: block;
    position: relative;
    margin-bottom: @spacing-base;

    > .tab-control {
        width: 100%;
        height: (@font-size-base * @line-height-base);
        z-index: @z-index-card - 10;
        > button.button--link {
            background-color: transparent;
            color: @color-gray-lighter;
        }
    }

    > .tab-bar{
        display: block;
        border-top: 1px dotted transparent;
        border-bottom: 1px solid @color-gray-lighter;
        font-size: @font-size-base;
        margin-bottom: @spacing-medium;
        padding: 0;
        white-space: nowrap;
        overflow:hidden;
        .clearfix();

        > .tab-bar-item {
            float: left;
            padding: @padding-base;
            cursor:  pointer;
            display: block;
            transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
            border-bottom: 2px solid transparent;

            &:focus {
                color: tint(@color-default, 25%);
            }

            &[aria-selected=true] {
                border-color: @color-default;
                color: @color-default;
            }

            &[aria-disabled=true] {
                color: @color-disabled;
                cursor: default;
            }

            &:focus {
                color: @color-aider;
                border-color: @color-aider;
                outline: 0;
                &:after {
                    content:'';
                    position: absolute;
                }
            }
        }
    }

    &.advanced {
        > .tab-bar{
            border-top-color:@color-gray-lighter;
        }
    }
}

// Skin
.tab {
    &.tab--brand {
        .tab-skin(@color-brand)
    }
    &.tab--primary {
        .tab-skin(@color-primary)
    }
    &.tab--success {
        .tab-skin(@color-success)
    }
    &.tab--warning {
        .tab-skin(@color-warning)
    }
    &.tab--error {
        .tab-skin(@color-error)
    }
}





