.ripple {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  pointer-events: none;
  overflow: hidden;

  > .ripple-effect {
    position: absolute;
    width: 20px;
    height: 20px;
    margin-left: -10px;
    margin-top: -10px;
    border-radius: 100%;
    background-color: #000;
    background-color: rgba(0,0,0,0.05);
    transform: scale(1);
    transform-origin: 50%;
    opacity: 0;
    transition-duration: 800ms;

    &.restarting {
      opacity: .3;
    }

    &.active {
      opacity: .3;
    }

    &:not(.active):not(.restarting) {
      opacity: 0;
    }
  }
}