// Base
.warpper
{
	background-position: center;
	background-size: 100%;
    border-bottom: 1px solid @color-gray-lighter;
    width: 100%;
    padding: 0;
    &.warpper--solid
    {
        > .warpper-content
        {
            @media (min-width: @screen-small-min)
            {
                width: @warpper-small;
            }
            @media (min-width: @screen-medium-min)
            {
                width: @warpper-medium;
            }
            @media (min-width: @screen-large-min)
            {
                width: @warpper-large;
            }
            @media (min-width: @screen-huge-min)
            {
                width: @warpper-huge;
            }
            @media (min-width: @screen-wide-min)
            {
                width: @warpper-wide;
            }
        }
    }
    > .warpper-content
    {
        position: relative;

        max-width: @warpper-wide;
        margin: 0 auto;
        padding: 0 (@spacing-base / 2);
    }
    &:last-child {
        border-bottom: none;
    }
    &.advanced {
        padding: @spacing-base 0;
    }
}

