.grid
{
    height: auto;
    position: relative;
    display: block;
    margin-right: (@spacing-base / -2);
    margin-left: (@spacing-base / -2);

    .clearfix();
    > .grid-item
    {
        display: block;
        float: left;
        padding-left:  (@spacing-base / 2);
        padding-right: (@spacing-base / 2);
    }
    &.simple {
        margin-right: 0;
        margin-left: 0;
        > .grid-item {
            padding-left:  0;
            padding-right: 0;
        }
    }
    &.grid--guttered
    {
        margin-right: (@spacing-base / -2);
        margin-left: (@spacing-base / -2);
        > .grid-item {
          padding-left:  (@spacing-base / 2);
          padding-right: (@spacing-base / 2);
        }
    }
}

.make-grid-items(tiny);

@media (min-width: @screen-small-min){
    .make-grid-items(small);
}

@media (min-width: @screen-medium-min){
    .make-grid-items(medium);
}

@media (min-width: @screen-large-min){
    .make-grid-items(large);
}

@media (min-width: @screen-huge-min){
    .make-grid-items(huge);
}

@media (min-width: @screen-wide-min){
    .make-grid-items(wide);
}
