// Base
.menu {
    display: block;
    position: relative;
    font-size: @font-size-base;
    padding: 0;
    white-space: nowrap;
    overflow:hidden;
    .clearfix();

    > .menu-item {
        color: @color-gray-base;
        cursor:  pointer;
        transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
        > a {
            color: @color-gray-base;
            &:focus {
                color: @color-aider;
                border-color: @color-aider;
                outline: 0;
                &:after {
                    content:'';
                    position: absolute;
                }
            }
        }
        &.disabled {
            color: @color-disabled;
            cursor: default;
            > a {
                color: @color-disabled;
            }
        }
    }

    &.menu--vertical {
        margin-left: -1px;
        border-left: 1px solid transparent;
        margin-top:5px;
        margin-bottom: 5px;
        > .menu-item {
            float: none;
            padding: @padding-base;
            display: block;
            border-left: 2px solid transparent;
            border-bottom: 1px solid @color-gray-lighter;
            .text-overflow();
            &:last-child {
                border-bottom: 0;
            }
            &.menu-item--header {
                font-weight: bold;
                cursor: default;
            }
            &.activated {
                border-left-color: @color-default;
                color: @color-default;
                > a {
                    color: @color-default;
                }
            }
        }
    }

    &.menu--horizontal {
        border-top: 1px dotted transparent;
        border-bottom: 1px solid @color-gray-lighter;
        margin-bottom: @spacing-medium;

        > .menu-item {
            float: left;
            padding: @padding-base;
            display: block;
            border-bottom: 2px solid transparent;

            &.menu-item--header {
                display: none;
            }
            &.activated {
                border-bottom-color: @color-default;
                color: @color-default;
                > a {
                    color: @color-default;
                }
            }
        }
    }

    &.menu--inline {
        > .menu-item {
            padding: 0;
            display: inline-block;
            float: none;
            margin-right: @spacing-large;
            &:last-child {
                margin-right: 0;
            }
            &.menu-item--header {
                display: none;
            }
            &.activated {
                color: @color-default;
                > a {
                    color: @color-default;
                }
            }
            + .menu-item:before{
                content: "/";
                color: @color-gray-lighter;
                padding: 0 @spacing-small;
                font-size: @font-size-small;
                margin-left: -@spacing-large;
            }
        }
    }

}

// Skin
.menu {
    &.menu--vertical {
        &.menu--default {
            .menu-vertical-skin(@color-default);
        }
        &.menu--brand {
            .menu-vertical-skin(@color-brand);
        }
        &.menu--primary {
            .menu-vertical-skin(@color-primary);
        }
        &.menu--success {
            .menu-vertical-skin(@color-success);
        }
        &.menu--warning {
            .menu-vertical-skin(@color-warning);
        }
        &.menu--error {
            .menu-vertical-skin(@color-error);
        }
    }

    &.menu--horizontal {
        &.menu--default {
            .menu-horizontal-skin(@color-default);
        }
        &.menu--brand {
            .menu-horizontal-skin(@color-brand);
        }
        &.menu--primary {
            .menu-horizontal-skin(@color-primary);
        }
        &.menu--success {
            .menu-horizontal-skin(@color-success);
        }
        &.menu--warning {
            .menu-horizontal-skin(@color-warning);
        }
        &.menu--error {
            .menu-horizontal-skin(@color-error);
        }
    }

    &.menu--inline {
        &.menu--default {
            .menu-inline-skin(@color-default);
        }
        &.menu--brand {
            .menu-inline-skin(@color-brand);
        }
        &.menu--primary {
            .menu-inline-skin(@color-primary);
        }
        &.menu--success {
            .menu-inline-skin(@color-success);
        }
        &.menu--warning {
            .menu-inline-skin(@color-warning);
        }
        &.menu--error {
            .menu-inline-skin(@color-error);
        }
    }
}