// Base
.layout
{
    min-height: 100%;
    > .layout-header
    {
        z-index: @z-index-layout;

        width: 100%;
        height: (@layout-header-height + .125rem);

        border-top: 1px solid @color-gray-lighter;
        border-bottom: 1px solid @color-gray-lighter;
        background: @color-white;

    }
    > .layout-necker
    {
        border-bottom: 1px dotted @color-gray-lighter;
        background: lighten(@color-gray-lighter, 10%);
        opacity: 0.95;
    }
    > .layout-mesoer
    {
        padding: 0;
        min-height: 30rem;
    }
    > .layout-footer
    {
        border-top: 1px dotted @color-gray-lighter;
        border-bottom: 1px solid @color-gray-lighter;
        background: @color-white;
    }
    /* 高级布局 */
    &.advanced
    {
        > .layout-header {
            position: fixed;
            top:auto;
            bottom: 0;
        }
        > .layout-necker
        {
            border-top: 1px solid @color-gray-lighter;
        }
        > .layout-mesoer
        {
            padding-bottom: @layout-header-height;
        }
        > .layout-footer
        {
            padding-bottom: @layout-header-height;

            border-bottom: 1px solid @color-gray-lighter;
        }
        > .layout-mesoer + .layout-footer
        {
            margin-top: -@layout-header-height;
        }
        @media (min-width: @screen-medium-min)
        {
            > .layout-header
            {
                top: 0;
                bottom: auto;
            }
            > .layout-mesoer
            {
                padding-top: @layout-header-height;
                padding-bottom: 0;
            }
            > .layout-footer
            {
                padding-bottom: 0;
            }
            > .layout-header + .layout-necker
            {
                padding-top: @layout-header-height;
                border-top: 0;
            }
            > .layout-necker + .layout-mesoer
            {
                padding-top: 0;
            }
            > .layout-mesoer + .layout-footer
            {
                margin-top: 0;
            }
        }
    }
}

// Addon
.layout {
    /* 普通布局 滑动头部 */
    > .layout-header {
        .header {
            > .header-navbar {
                > .navbar {
                    > .navbar-content {
                        .dropdown {
                            &.dropdown--navbar {
                                text-align: right;
                            }
                        }
                    }
                }
            }
            /* 高级标题 */
            &.advanced {
                > .header-navbar {
                    > .navbar {
                        &.advanced {
                            > .navbar-content {
                                .dropdown {
                                    &.dropdown--navbar {
                                        text-align: center;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    /* 高级布局 固定头部 */
    &.advanced {
        > .layout-header {
            .header {
                > .header-navbar {
                    > .navbar {
                        > .navbar-content {
                            .dropdown {
                                &.dropdown--navbar {
                                    text-align: center;
                                    > .dropdown-content {
                                        top:auto;
                                        bottom: 100%;
                                    }
                                }
                            }
                        }
                        > .navbar-surplus {
                            > .dropdown {
                                > .dropdown-content {
                                    top:auto;
                                    bottom: 100%;
                                    @media (min-width: @screen-medium-min) {
                                        bottom: auto;
                                        top:100%;
                                    }
                                }
                            }
                        }
                        &.advanced {
                            > .navbar-content {
                                .dropdown {
                                    &.dropdown--navbar {
                                        text-align: right;
                                    }
                                }
                            }
                        }
                    }
                }
                /* 高级标题 */
                &.advanced {
                    > .header-navbar {
                        > .navbar {
                            &.advanced {
                                > .navbar-content {
                                    .dropdown {
                                        &.dropdown--navbar {
                                            text-align: center;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}