// Base
.card {
    position: relative;
    border: 1px solid @color-gray-lighter;
    border-radius: @border-radius-tiny;
    margin-bottom: @spacing-base;
    background: @color-snow;
    padding-bottom: 1px;
    &:focus {
        outline: 0;
    }

    > .card-control {
        position: absolute;
        top: 0;
        right: @spacing-medium;
        z-index: @z-index-card + 100;
        > .button {
            color: @color-gray-lighter!important;
        }
    }

    > .card-front {
        background: @color-white;
        border-radius: @border-radius-tiny;
        &:focus {
            outline: 0;
        }

        > .card-front-head {
            padding: @padding-base;
            position: relative;
            .border-top-radius(@border-radius-tiny);
        }

        > .card-front-body {
            padding: @spacing-medium;
        }

        > .card-front-foot {
            padding: @padding-base;
            border-top: 1px solid @color-gray-lighter;
            background-color: @background-foot;
            .border-bottom-radius(@border-radius-tiny);
        }
    }

    > .card-back {
        position: absolute;
        z-index: @z-index-card + 50;
        width: 100%;
        top: 0;
        border-radius: @border-radius-tiny;
        padding: @padding-base;
        background: @color-snow;
        &:focus {
            outline: 0;
        }
    }
}