// Generate
.field-generate(@color) {
  > .field-help {
    color: @color;
  }
  > .field-control {
    > input,
    > textarea {
      background-image: linear-gradient(@color, @color), linear-gradient(@color, @color);
    }
  }
  &.focused {
    > .field-label {
      color: lighten(@color,5%);
    }
  }
  &.visited {
    > .field-label {
      color: lighten(@color,5%);
    }
  }
}

