// skin
.menu-vertical-skin(@text-color) {
  > .menu-item {
    &.activated {
      border-left-color: @text-color;
      color: @text-color;
      > a {
        color: @text-color;
      }
    }
  }
}

.menu-horizontal-skin(@text-color) {
  > .menu-item {
    &.activated {
      border-bottom-color: @text-color;
      color: @text-color;
      > a {
        color: @text-color;
      }
    }
  }
}

.menu-inline-skin(@text-color) {
  > .menu-item {
    &.activated {
      color: @text-color;
      > a {
        color: @text-color;
      }
    }
  }
}


