// Typegraphy

address,
caption,
cite,
code,
dfn,
em,
th,
var
{
    font-weight: 400;
}

/* 调整上下标的位置 */
sub,
sup
{
    font-size: 75%;

    position: relative;
}

:root sub,
:root sup
{
    vertical-align: baseline;
}

sup
{
    top: -.5em;
}

sub
{
    bottom: -.25em;
}

/* 一致化 horizontal rule */
hr
{
    height: 1rem;
    margin-bottom: 1rem;

    border: none;
    border-bottom: 1px solid @color-gray-lighter;
}

/* 一致化某些元素的展示 */
acronym,
abbr
{
    font-variant: normal;

    border-bottom: 1px dotted;
}

abbr
{
    cursor: help;
}

del
{
    text-decoration: line-through;
}

q:before,
q:after
{
    content: '';
}

ins
{
    text-decoration: none;
}

u
{
    text-decoration: none;

    border-bottom: 1px solid;
}

small,
figcaption
{
    font-size: .875em;

    color: @color-gray-light;
}

strong,
b
{
    font-weight: bold;

    color: @color-gray-base;
}

/* 段落引用 */
blockquote
{
    position: relative;

    margin: 1rem 0rem;
    padding-left: 1rem;

    color: @color-gray-light;
    border-left: 1px solid @color-success;
    @media (min-width: @screen-medium-min)
    {
        margin: 1rem 0rem 1rem 2rem;
    }
}

/* 高亮显示 */
mark
{
    border-bottom: 1px solid #ffedce;
    background: @background-mark;
}

/* 代码片断 */
pre,
code,
pre tt
{
    font-family: @font-code-family;
}

pre
{
    display: block;

    padding: 1rem 1.5rem;

    border: 1px solid #ddd;
    background: #f8f8f8;

    -webkit-overflow-scrolling: touch;
}

// heading
h1,
h2,
h3,
h4,
h5,
h6
{
    font-family: @font-head-family;
    font-weight: 100;
    line-height: @line-height-base;

    margin-top: 1.2rem;
    margin-bottom: .6rem;

    color: @color-gray-base;
}

h1 {font-size: @font-size-h1;}
h2 {font-size: @font-size-h2;}
h3 {font-size: @font-size-h3;}
h4 {font-size: @font-size-h4;}
h5 {font-size: @font-size-h5;}
h6 {font-size: @font-size-h6;}

/* 文章排版 */
article p {
    margin-bottom: 1rem;
}









