// Color
@color-base             : #303030; // 百草霜
@color-snow             : #fffafa; // 雪白色
@color-white            : #ffffff; // 纯白色
@color-candle           : #e7e5d0; // 蜡白色
@color-rice             : #e7e5d0; // 米白色
@color-brand            : #b8844f; // 棕茶色
@color-aider            : #bca590; // 纸棕色
@color-disabled         : #cccccc; // 普通灰

@color-default          : #93a2a9; // 正灰色
@color-primary          : #2578b5; // 深竹月
@color-success          : #21A675; // 松柏绿
@color-warning          : #e8853b; // 桔黄色
@color-error            : #d54b44; // 榴花红

@color-gray-base        : #3f3f3c; // 油烟墨
@color-gray-darker      : darken(@color-gray-dark, 20%);
@color-gray-dark        : darken(@color-gray-base, 5%);
@color-gray-light       : lighten(@color-gray-base, 45%);
@color-gray-lighter     : lighten(@color-gray-light, 20%);

// Spacing
@spacing-tiny           : 0.125rem;
@spacing-small          : 0.3125rem;
@spacing-medium         : 0.625rem;
@spacing-large          : 0.875rem;
@spacing-huge           : 1.125rem;
@spacing-wide           : 1.25rem;
@spacing-base           : 1rem;

@padding-tiny           : 0rem 0.125rem;
@padding-small          : 0.125rem 0.3125rem;
@padding-medium         : 0.3125rem 0.625rem; // 默认大小
@padding-large          : 0.625rem 0.875rem;
@padding-huge           : 0.875rem 1.125rem;
@padding-wide           : 1.125rem 1.25rem;
@padding-base           : 0.3125rem 0.625rem;

@border-radius-tiny     : 1px;
@border-radius-small    : 4px;
@border-radius-medium   : 6px;
@border-radius-large    : 9px;
@border-radius-huge     : 12px;
@border-radius-wide     : 14px;
@border-radius-base     : 6px;

// Background
@background-base        : @color-snow;
@background-disabled    : #f3f5f7;
@background-html 		: @color-snow;
@background-body 		: @color-snow;

@background-mark        : @color-rice;
@background-selection   : @color-candle;
@background-foot        : #f8fafa;

// Screen
@screen-tiny            : 320px;  // iphone 5s
@screen-small           : 540px;  // iphone 6s plus
@screen-medium          : 768px;  // ipad mini
@screen-large           : 936px;  // desktop
@screen-huge            : 1080px; // macbook 11
@screen-wide            : 1280px; // macbook 13
@screen-tiny-min        : @screen-tiny;
@screen-small-min       : @screen-small;
@screen-medium-min      : @screen-medium;
@screen-large-min       : @screen-large;
@screen-huge-min        : @screen-huge;
@screen-wide-min        : @screen-wide;
@warpper-tiny           : 320px;  // 320px  20rem
@warpper-small          : 512px;  // 512px  32rem
@warpper-medium         : 768px;  // 768px  48rem
@warpper-large          : 936px;  // 936px  52rem
@warpper-huge           : 1080px; // 1080px 60rem
@warpper-wide           : 1152px; // 1152px 64rem

// Grid
@grid-items             : 24;

// Fontfamily
@font-family            : "Merriweather","PingFang SC","Lantinghei SC","Microsoft Yahei","Hiragino Sans GB","Microsoft Sans Serif","WenQuanYi Micro Hei",Helvetica,sans-serif;
@font-head-family       : "PingFang SC",Verdana, Helvetica Neue,"Microsoft Yahei","Hiragino Sans GB","Microsoft Sans Serif",sans-serif;
@font-code-family       : consolas, monaco, "Source Code Pro", hack, monospace;

@font-size-base         : 1rem;      // small=16px, medium=18px
@font-size-tiny         : 0.625rem;  // small=10px, large=14px
@font-size-small        : 0.875rem;  // small=14px, large=16px
@font-size-medium       : 1rem;      // small=16px, medium=18px
@font-size-large        : 1.125rem;  // small=18px, large=20.25px

@font-size-h1           : 2.25rem;
@font-size-h2           : 2rem;
@font-size-h3           : 1.75rem;
@font-size-h4           : 1.5rem;
@font-size-h5           : 1.25rem;
@font-size-h6           : 1rem;

@line-height-base       : 1.5;      // small=24px, medium=27px
@line-height-small      : 1.25;     // small=20px, medium=22.5px

// Z-link
@z-index-card           :99;
@z-index-layout         :999;
@z-index-dropdown       :9999;
@z-index-portal         :99999;

// Base
@link-color  			: @color-aider;
@link-hover-color       : tint(@link-color, 20%);
@link-active-color      : shade(@link-color, 5%);
@link-hover-decoration  : none;

// Shadow
@shadow-umbra-opacity   : 0.2;
@shadow-penumbra-opacity: 0.14;
@shadow-ambient-opacity : 0.12;

// Layout
@layout-header-height   : 3rem;

// Navbar
@navbar-height-small    : 1.5rem;
@navbar-height-base     : 2rem;
@navbar-height-large    : @layout-header-height;

// Note
@side-width-small       : 3.625rem;
@side-width-medium      : 6.835rem;

// Avatar
@avatar-width-bitty     : 1.5rem;    // small=24px,medium=27px;
@avatar-width-tiny      : 2.25rem;   // small=36px,medium=40.5px;
@avatar-width-small     : 3rem;      // small=48px,medium=54px
@avatar-width-medium    : 6.835rem;  // small=96px,medium=108px
@avatar-width-large     : 9rem;      // small=144px,medium=162px
@avatar-width-huge      : 12rem;     // small=192px,medium=216px


