// Base
.button
{
    font-weight: 100;
    line-height: @line-height-base;

    position: relative;

    display: inline-block;

    margin: 0;
    margin-right: @spacing-small;
    padding: @padding-base;

    transition: box-shadow .2s cubic-bezier(.4, 0, 1, 1),
    background-color .2s cubic-bezier(.4, 0, .2, 1),
    color .2s cubic-bezier(.4, 0, .2, 1);
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: @font-size-base;

    color: @color-gray-light;
    border: 1px solid @color-gray-lighter;
    border-radius: @border-radius-base;
    outline: none;

    touch-action: manipulation;
    -webkit-appearance: none;
    background: @color-white;
    > *
    {
        pointer-events: none;
    }
    &:hover
    {
        color: tint(@color-gray-light, 25%);
        border-color: tint(@color-gray-lighter, 25%);
        background-color: tint(@color-gray-lighter, 25%);
    }
    &:active,
    &.activated
    {
        color: shade(@color-gray-light, 5%);
        border-color: shade(@color-gray-lighter, 5%);
        background-color: shade(@color-gray-lighter, 5%);
    }
    &:last-child
    {
        margin-right: 0;
    }
    &.disabled,
    &[disabled],
    fieldset[disabled] &
    {
        &,
        &:hover,
        &:active,
        &.activated
        {
            color: @color-disabled;
            background-color: @background-disabled;
            border-color: @color-gray-lighter;
        }
    }

    &.button--link
    {
        .button-kind-link(@color-gray-light);
    }

    &.button--tiny
    {
        padding: @padding-tiny;
        font-size: @font-size-tiny;
        border-radius: @border-radius-tiny;
    }

    &.button--small
    {
        padding: @padding-small;
        font-size: @font-size-small;
        border-radius: @border-radius-small;
    }
    &.button--medium
    {
        padding: @padding-medium;
        font-size: @font-size-medium;
        border-radius: @border-radius-medium;
    }
    &.button--large
    {
        padding: @padding-large;
        font-size: @font-size-medium;
        border-radius: @border-radius-large;
    }
    &.button--huge
    {
        padding: @padding-huge;
        font-size: @font-size-large;
        border-radius: @border-radius-huge;
    }
    &.button--wide
    {
        padding: @padding-wide;
        font-size: @font-size-large;
        border-radius: @border-radius-wide;
    }

    &.button--block
    {
        display: block;

        width: 100%;
        margin-bottom: @spacing-small;
        &:last-child
        {
            margin-bottom: 0;
        }
    }

    &.button--circle
    {
        border-radius: 50%;
    }
}

// Skin
.button {
    &.button--default
    {
        .button-skin(@color-snow; @color-default);
        &.button--hollow
        {
            .button-kind-hollow(@color-default);
        }
        &.button--link
        {
            .button-kind-link(@color-default);
        }
