// Base
.dropdown {
    position: relative;
    display: inline-block;

    > .dropdown-trigger {
        display: inline-block;
    }

    > .dropdown-content {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: @z-index-dropdown;
        float: left;
        text-align: left;
        display: none;
        width: 10rem;
        margin:2px 0;
        padding: @padding-base;
        background-color: @color-white;
        background-clip: padding-box;
        border: 1px solid @color-gray-lighter;
        border-radius: @border-radius-tiny;
        line-height: @line-height-base;

        &.dropdown-content--left {
            left: 5px;
            right: auto;
        }
        &.dropdown-content--right {
            right: 5px;
            left: auto;
        }
        &.dropdown-content--center {
            left: 50%;
            margin-left: -5rem;
        }
    }

    &.activated {
        > .dropdown-content {
            display: block;
        }
    }

    &.dropdown--menu {
        > .dropdown-content {
            padding: 0;
        }
    }
}

// size
.dropdown-content {
    &.dropdown-content--tiny {
        width: 6.25rem;
        &.dropdown-content--center {
            margin-left: -3.125rem;
        }
    }
    &.dropdown-content--small {
        width: 8rem;
        &.dropdown-content--center {
            margin-left: -4rem;
        }
    }
    &.dropdown-content--medium {
        width: 10rem;
        &.dropdown-content--center {
            margin-left: -5rem;
        }
    }
    &.dropdown-content--large {
        width: 12.25rem;
        &.dropdown-content--center {
            margin-left: -6.125rem;
        }
    }
    &.dropdown-content--huge {
        width: 15rem;
        &.dropdown-content--center {
            margin-left: -7.5rem;
        }
    }
    &.dropdown-content--wide {
        min-width: 19rem;
        &.dropdown-content--center {
            margin-left: -9.5rem;
        }
    }
}

// type
.dropdown {
    &.dropdown--navbar {
        display: table-cell;
        width: 1%;
        text-align: center;
        border-right: 1px dotted @color-gray-lighter;

        &:last-child {
            border-right: 0;
        }

        > .dropdown-content {
            padding: 0;
        }
        @media (min-width: @screen-medium-min) {
            display: inline-block;
            width: auto;
            border-right: 0;
            > .dropdown-trigger {
                display: none;
            }
            > .dropdown-content {
                position: relative;
                width: auto;
                left: 0;
                margin-top: 0;
                margin-bottom: 0;
                margin-left: 0;
                display: block;
                float: left;
                border:0;

                > .menu {
                    &.menu--vertical {
                        margin:0;
                        > .menu-item {
                            float: left;
                            border: 0;
                            &.activated {
                                border-left-color: transparent;
                            }
                            &.menu-item--header {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}
