/* 清除浮动和流动布局 */
.clearfix
{
    .clearfix();
}

.pull
{
    .clearfix();
}

.pull-left
{
    float: left;
}

.pull-right
{
    float: right;
}

/* 显示隐藏与可见行 */
.hidden
{
    display: none !important;
}

.invisible
{
    visibility: hidden;
}

.show
{
    display: block !important;
}